<template>
  <div class="nav-main">
    <header>
      <div class="app-center">
        <nav class="nav">
          <span><img src="../assets/images/logo.fc6bc3c1.png" alt=""/></span
          ><router-link :class="{ 'nav-index': $route.path == '/' }" to="/"
            >HOME</router-link
          >
          <router-link
            :class="{ 'nav-index': $route.path == '/blog' }"
            to="blog"
            >BLOG</router-link
          ><router-link
            :class="{ 'nav-index': $route.path == '/about' }"
            to="about"
            >ABOUT</router-link
          >
        </nav>
        <div class="user">
          <span v-if="!username" @click="login">登入/注册</span>
          <span v-else @click="dialogVisible = true"
            >{{ username
            }}<img class="icon" src="../assets/images/noavatar_small.png" alt=""
          /></span>
        </div>
        <div class="times" @click="change3d">
          <a href="javascript:;"></a><time>{{ nowDateTime }}</time>
        </div>
      </div>
    </header>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
    >
      <span>确定要退出当前用户吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="exitLogin">确 定</el-button>
      </span>
    </el-dialog>
    <login
      :isShow="isLoginStatus"
      @closeLogin="closeLogin"
      @successLogin="successLogin"
    ></login>
  </div>
</template>

<script>
import store from '../store'
import login from './login/login.vue'
export default {
  data() {
    return {
      nowDateTime: '',
      isLoginStatus: false,
      username: '',
      sharedState: store.state,
      dialogVisible: false,
    }
  },
  created() {
    //刷新时间
    setInterval(this.getTime, 500)
    //获取用户名字
    if (localStorage.getItem('token')) {
      this.getusername()
    }
  },
  methods: {
    handleClose(done) {
      done()
    },
    exitLogin() {
      this.dialogVisible = false
      window.localStorage.removeItem('token')
      this.username = ''
      store.setMessageAction(this.username)
    },
    successLogin(obj) {
      let { username } = obj
      this.username = username
      localStorage.setItem('online_name', this.username)
      store.setMessageAction(this.username)
    },
    closeLogin() {
      this.isLoginStatus = false
    },
    login() {
      this.isLoginStatus = true
    },
    async getusername() {
      const res = await this.$http.post(`u/getusername`, this.model)
      this.username = res.data.username
      store.setMessageAction(this.username)
      this.id = res.data._id
      this.ready = true
    },
    change3d() {
      window.location.href = 'http://localhost:3000/3d'
    },
    getTime() {
      var time = new Date()
      var hours = time.getHours() > 9 ? time.getHours() : '0' + time.getHours() //时
      var minutes =
        time.getMinutes() > 9 ? time.getMinutes() : '0' + time.getMinutes() //分
      var seconds =
        time.getSeconds() > 9 ? time.getSeconds() : '0' + time.getSeconds() //秒
      var mydate = hours + ':' + minutes + ':' + seconds
      this.nowDateTime = mydate
    },
  },
  components: { login },
}
</script>

<style>
.app-center {
  width: calc(100% - 120px);
  margin: 0 auto;
  position: relative;
}
header {
  width: 100%;
  background-color: #fff;
  border-bottom: 1px solid #111;
  height: 36px;
}
.nav-main header nav::after {
  content: '';
  display: block;
  clear: both;
}
.nav-main header .times {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
}
.nav-main header .user {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 100px;
  height: 100%;
  padding: 7px 10px;
  float: left;
}
.nav-main header .user span {
  display: block;
  font-size: 16px;
  font-weight: bold;
}
.icon {
  float: right;
  height: 18px;
  width: 18px;
}
.nav-main header .times a {
  display: block;
  padding: 7px 10px;
  float: left;
}
.nav-main header .times time {
  float: left;
  display: block;
  font-size: 16px;
  font-weight: bold;
  line-height: 38px;
}
.nav-main header .mobile-logo {
  position: relative;
}
.nav-main header .mobile-logo img {
  display: block;
  position: absolute;
  top: 9px;
  left: 0;
}
.nav-main header .mobile-logo span {
  font-size: 1rem;
  font-weight: bold;
  line-height: 38px;
  padding-left: 32px;
}
.nav-main header .mobile-menu {
  display: block;
  position: absolute;
  z-index: 999;
  top: 8px;
  right: 0;
  width: 22px;
  height: 22px;
}
.nav-main header .mobile-menu.mobile-menu-active div:nth-child(1) {
  -webkit-transform: translate(0, 6px) rotate(45deg);
  -ms-transform: translate(0, 6px) rotate(45deg);
  transform: translate(0, 6px) rotate(45deg);
}
.nav-main header .mobile-menu.mobile-menu-active div:nth-child(2) {
  -webkit-transform: translate(-3px, 0);
  -ms-transform: translate(-3px, 0);
  transform: translate(-3px, 0);
  opacity: 0;
}
.nav-main header .mobile-menu.mobile-menu-active div:nth-child(3) {
  -webkit-transform: translate(0, -6px) rotate(-45deg);
  -ms-transform: translate(0, -6px) rotate(-45deg);
  transform: translate(0, -6px) rotate(-45deg);
}
.nav-main header .mobile-menu div {
  background: #111;
  width: 100%;
  height: 2px;
  margin: 4px 0 0;
  -webkit-transition: all 0.15s cubic-bezier(0, 0.3, 0.8, 1) 0s;
  -o-transition: all 0.15s cubic-bezier(0, 0.3, 0.8, 1) 0s;
  transition: all 0.15s cubic-bezier(0, 0.3, 0.8, 1) 0s;
}
.nav-main header .mobile-nav {
  visibility: hidden;
  position: fixed;
  background-color: #ffd03f;
  top: 0;
  left: 0;
  z-index: 900;
  width: 100vw;
  height: 100vh;
  padding-top: 14vh;
  opacity: 0;
  -webkit-transition: opacity 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0s;
  -o-transition: opacity 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0s;
  transition: opacity 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0s;
}
.nav-main header .mobile-nav a {
  display: block;
  line-height: 2.6;
  text-align: center;
  font-size: 1.8rem;
  text-decoration: underline;
  -webkit-transform: translate(0, 8px);
  -ms-transform: translate(0, 8px);
  transform: translate(0, 8px);
  opacity: 0;
}
.nav-main header .mobile-nav a:nth-child(1) {
  -webkit-transition: all 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0s;
  -o-transition: all 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0s;
  transition: all 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0s;
}
.nav-main header .mobile-nav a:nth-child(2) {
  -webkit-transition: all 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0.07s;
  -o-transition: all 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0.07s;
  transition: all 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0.07s;
}
.nav-main header .mobile-nav a:nth-child(3) {
  -webkit-transition: all 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0.15s;
  -o-transition: all 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0.15s;
  transition: all 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0.15s;
}
.nav-main header .mobile-nav a:nth-child(4) {
  -webkit-transition: all 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0.23s;
  -o-transition: all 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0.23s;
  transition: all 0.25s cubic-bezier(0, 0.3, 0.8, 1) 0.23s;
}
.nav-main header .mobile-nav.mobile-in-active {
  visibility: visible;
  opacity: 1;
}
.nav-main header .mobile-nav.mobile-in-active a {
  -webkit-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  transform: translate(0, 0);
  opacity: 1;
}
.nav-main header .mobile-nav.mobile-live-active {
  visibility: visible;
  opacity: 0;
}
.nav-main header .mobile-nav.mobile-live-active a {
  -webkit-transform: translate(0, 8px);
  -ms-transform: translate(0, 8px);
  transform: translate(0, 8px);
  opacity: 0;
}
.nav > span {
  display: block;
  padding: 9px 12px 0 0;
  float: left;
  font-weight: bolder;
  color: #111;
  position: relative;
  z-index: 1;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.nav span ~ a {
  display: block;
  float: left;
  font-weight: bolder;
  color: #111;
  position: relative;
  z-index: 1;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0 10px;
  cursor: pointer;
  line-height: 36px;
  font-size: 1rem;
}
.nav span ~ a::after {
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -14px;
  margin-top: -14px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  border: 3px solid #ffd03f;
  background-color: #fff;
  z-index: -1;
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
  transform: scale(0);
  -webkit-transition: -webkit-transform 0.15s
    cubic-bezier(0.18, 1.29, 0.89, 1.24);
  transition: -webkit-transform 0.15s cubic-bezier(0.18, 1.29, 0.89, 1.24);
  -o-transition: transform 0.15s cubic-bezier(0.18, 1.29, 0.89, 1.24);
  transition: transform 0.15s cubic-bezier(0.18, 1.29, 0.89, 1.24);
  transition: transform 0.15s cubic-bezier(0.18, 1.29, 0.89, 1.24),
    -webkit-transform 0.15s cubic-bezier(0.18, 1.29, 0.89, 1.24);
}
.nav span ~ a:hover {
  color: #111111;
  text-decoration: none;
}
.nav span ~ a:hover::after {
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
}
.nav a.nav-index::after {
  background-color: #ffd03f;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
  -webkit-animation: indexs 0.15s ease-out forwards;
  animation: indexs 0.15s ease-out forwards;
}
@-webkit-keyframes indexs {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes indexs {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
</style>
