<script>
import store from '../store'
import keywords from '../assets/weiguici'
import marked from 'marked'
import dayjs from 'dayjs'
export default {
  filters: {
    toDate(val) {
      return dayjs(val).format('MM/DD')
    },
  },
  name: 'home',
  data() {
    return {
      sharedState: store.state,
      iswrite: false,
      model: {
        msg: '',
      },
      index: 0,
      loading: false,
      content: [],
      keyMsg: '',
    }
  },
  methods: {
    keyWordUpdata(objStr, keywordArr) {
      var bool = false
      for (let i = 0; i < keywordArr.length; i++) {
        var reg = new RegExp(keywordArr[i], 'ig')

        var msg = objStr.match(reg)
        // var reg = keywordArr.includes(objStr[i])
        // console.log(reg)
        if (msg) {
          this.keyMsg = msg
          bool = true
        }
      }
      return bool
    },
    change(i) {
      this.index = i
      this.model.msg = ''
      this.iswrite = false
      this.goTop()
    },
    goTop() {
      this.$refs.main.scrollTop = 0
    },
    getDate(t) {
      var time = new Date(t)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      var hh = time.getHours() > 9 ? time.getHours() : '0' + time.getHours() //时
      var mm =
        time.getMinutes() > 9 ? time.getMinutes() : '0' + time.getMinutes() //分
      var ss =
        time.getSeconds() > 9 ? time.getSeconds() : '0' + time.getSeconds() //秒
      var mydate = y + '/' + m + '/' + d + ' ' + hh + ':' + mm + ':' + ss
      return mydate
    },
    write() {
      this.model.id = this.content[this.index]._id
      this.model.time = new Date().getTime()

      if (!this.sharedState.message) {
        this.$message({
          type: 'error',
          message: '请先登入',
        })
      } else {
        this.model.name = this.sharedState.message
        this.fetchWriteTalk()
      }
    },
    tomk(content) {
      return marked(content)
    },
    async fetchWriteTalk() {
      let data = JSON.parse(JSON.stringify(this.model))
      if (this.keyWordUpdata(data.msg, keywords.all)) {
        alert(`发布失败，存在违规词"${this.keyMsg}"`)
        this.model.msg = ''
        return
      }

      const res = await this.$http.put('u/writemsg', this.model)
      this.content[this.index].msg.push(data)
      this.model.msg = ''
      this.$message({
        type: 'success',
        message: '发布成功',
      })
    },
    async fetchNewContent() {
      const res = await this.$http.get('content')
      this.content = res.data.reverse()
      this.loading = true
    },
  },
  created() {
    this.fetchNewContent()
  },
  components: {},
}
</script>

