<template>
  <div>
    <div class="login" v-if="login_show">
      <div class="dialog fade-enter-done">
        <div class="dialog-bg"></div>
        <div class="animate__animated animate__rubberBand userCenter__main">
          <div class="figure-pop figure-style-white">
            <p style="cursor: auto;">
              <a title="关闭" href="javascript:;" @click="close"></a>个人中心
            </p>
            <div class="home__sginDiv___2tWW_">
              <img src="../assets/images/mail.png" />
              <div class="home__sginInput___2ldXT">
                <input
                  type="text"
                  readonly="readonly"
                  placeholder="用户名"
                  v-model="model.username"
                />
              </div>
              <div class="home__sginInput___2ldXT">
                <input
                  type="password"
                  placeholder="修改密码"
                  v-model="model.password"
                />
              </div>
              <div class="home__sginInput___2ldXT">
                <button @click="login">
                  修改
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'usercenter',
  props: ['isShow', 'userName', '_id'],
  data: function() {
    return {
      id: this._id,
      model: {},
    }
  },
  computed: {
    login_show() {
      return this.isShow
    },
  },
  watch: {
    isShow() {
      if (!this.isShow) return
      this.model.username = this.userName
      this.id = this._id
    },
  },
  methods: {
    close() {
      this.$emit('closeUserCenter')
    },
    async login() {
      if (this.id) {
        let res = await this.$http.put(`u/updateuser/${this.id}`, this.model)
        this.$message({
          type: 'success',
          message: '修改成功',
        })
        this.model = {}
        this.close()
      } else {
        let res = await this.$http.post(`u/getusername`, this.model)
        this.id = res.data._id
        this.model.username = res.data.username
        await this.$http.put(`u/updateuser/${this.id}`, this.model)
        this.$message({
          type: 'success',
          message: '修改成功',
        })
        this.model = {}
        this.close()
      }
    },
  },
}
</script>

