<template>
  <div class="login-container">
    <login
      :isShow="isLoginStatus"
      @closeLogin="closeLogin"
      @successLogin="successLogin"
    ></login>
  </div>
</template>

<script>
import login from '../components/login/login'
export default {
  data() {
    return {
      isLoginStatus: true,
    }
  },
  methods: {
    closeLogin() {
      this.$router.push('/')
    },
  },
  components: { login },
}
</script>
