<template>
  <div>
    <div class="login" v-if="login_show" ref="login">
      <div class="dialog fade-enter-done">
        <div class="dialog-bg"></div>
        <div class="login__main animate__animated animate__tada">
          <div class="figure-pop figure-style-white ">
            <p style="cursor: auto;">
              <a title="关闭" href="javascript:;" @click="close"></a
              >{{ isLogin ? '登入' : '注册' }}
            </p>
            <div class="home__sginDiv___2tWW_">
              <span title="点击我切换登入/注册" @click="change">
                <img
                  src="../../assets/images/dl.png"
                  :class="[isLogin ? 'dorz_show' : '', 'dorz_dl']"
                />
                <img
                  class="dorz_zc"
                  src="../../assets/images/zc.png"
                  :class="[!isLogin ? 'dorz_show' : '', 'dorz_dl']"
                />
                <img class="dorz_line" src="../../assets/images/line.png" />
                <img src="../../assets/images/mail.png" />
              </span>
              <div class="home__sginInput___2ldXT">
                <input
                  type="text"
                  placeholder="用户名"
                  v-model="model.username"
                />
              </div>
              <div class="home__sginInput___2ldXT">
                <input
                  type="password"
                  placeholder="密码"
                  v-model="model.password"
                />
              </div>
              <div class="home__sginInput___2ldXT">
                <button @click="login">
                  {{ isLogin ? '登入' : '注册' }}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import html2canvas from 'html2canvas'
import keywords from '../../assets/weiguici'
export default {
  name: 'login',
  props: ['isShow'],
  data: function() {
    return {
      isLogin: true,
      model: {},
      keyMsg: '',
      // sharedState: store.state,
    }
  },
  computed: {
    login_show() {
      if (this.isShow) {
        // console.log(this.toImage())
      }
      return this.isShow
    },
  },
  methods: {
    keyWordUpdata(objStr, keywordArr) {
      var bool = false
      for (let i = 0; i < keywordArr.length; i++) {
        var reg = new RegExp(keywordArr[i], 'ig')

        var msg = objStr.match(reg)
        // var reg = keywordArr.includes(objStr[i])
        // console.log(reg)
        if (msg) {
          this.keyMsg = msg
          bool = true
        }
      }
      return bool
    },
    toImage() {
      setTimeout(() => {
        html2canvas(this.$refs.login).then((canvas) => {
          let dataURL = canvas.toDataURL('image/png')
          console.log(dataURL)
        })
      })
    },
    close() {
      this.$emit('closeLogin')
    },
    async fetch_login() {
      const res = await this.$http.post(`u/login`, this.model)
      // 生成token
      localStorage.token = res.data.token
      //关闭login弹窗
      this.close()
      //跳转到首页
      // this.$router.push('/')
      this.model = {}
      //弹出信息
      this.$message({
        type: 'success',
        message: '登入成功',
      })
      this.$emit('successLogin', res.data)
    },
    login() {
      if (this.isLogin) {
        this.fetch_login()
      } else {
        this.fetch_register()
      }
    },
    async fetch_register() {
      if (!this.model.username) {
        alert(`用户名不能为空`)
        return
      }
      if (!this.model.password) {
        alert(`密码不能为空`)
        return
      }
      if (this.keyWordUpdata(this.model.username, keywords.all)) {
        alert(`注册失败，存在违规词"${this.keyMsg}"`)
        this.model.username = ''
        this.model.password = ''
        return
      }
      const res = await this.$http.post(`u/register`, this.model)
      this.$message({
        type: 'success',
        message: '注册成功',
      })
      this.model = {}
      this.isLogin = true
    },
    change() {
      this.isLogin = !this.isLogin
    },
  },
}
</script>

<style scoped lang="scss">
