  box-sizing: border-box;
}
.figure-pop-about > p {
  background-color: #fff;
  line-height: 40px;
  border-bottom: 1px solid #111;
  text-align: center;
  position: relative;
  font-size: 16px;
  border-radius: 18px 18px 0 0;
}
.figure-pop {
  position: absolute;
