.home__sginDiv___2tWW_ .home__sginInput___2ldXT::after {
  content: '';
  display: block;
  clear: both;
}
div[class*='figure-style-'] {
  border-radius: 18px;
  border: 1px solid #111;
}
.figure-style-white > p {
  background-color: #d8dce3;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
</style>
