            ><br /><span class="home__animate___2UEzF"
              >(●￣(ｴ)￣●)<strong>#Skr</strong
              ><i class="home__cursor___rh_V4"></i
            ></span>
          </div>
          <footer class="home__footer___38qW3">
            <ul>
              <li><b>const</b> Date = {{ getDate }}</li>
              <li><b>const</b> UseTime = 4s</li>
              <li><b>const</b> Add=哈尔滨理工大学</li>
