              </figure>
            </div>
            <div class="figure-flex" @click="showDnf">
              <figure>
                <img src="../assets/images/readme.png" alt="" />
                <figcaption>单机游戏</figcaption>
              </figure>
            </div>
            <div class="figure-flex" @click="showOnline">
              <figure>
                <img src="../assets/images/msg.png" alt="" />
                <figcaption>多人游戏</figcaption>
              </figure>
            </div>
            <div class="figure-flex" @click="showUserCenter">
