    <user-center
      :isShow="isUserCenterStatus"
      :userName="username"
      :_id="id"
      @closeUserCenter="closeUserCenter"
    ></user-center>
    <!-- chat -->
    <Chat
      :isShow="isChatShow"
      :WatchScroll="true"
      @closeChat="closeChat"
    ></Chat>
    <!-- dnf -->
    <Dnf :isShow="isDnfShow" :WatchScroll="true" @closeDnf="closeDnf"></Dnf>
