<script>
import store from '../store'
import UserCenter from './UserCenter.vue'
import Dnf from './../components/dnf'
import onlineGame from './../components/onlineGame'
export default {
  data() {
    return {
      username: '',
      isDnfShow: false,
      isOnlineGameShow: false,
      sharedState: store.state,
      id: '',
      isUserCenterStatus: false,
      isChatShow: false,
    }
  },
  computed: {
    getDate() {
      var time = new Date()
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      var mydate = y + '/' + m + '/' + d
      return mydate
    },
  },
  methods: {
    closeOnline() {
      this.isOnlineGameShow = false
    },
    closeDnf() {
      this.isDnfShow = false
    },
    closeChat() {
      this.isChatShow = false
    },
    isLogin(fn) {
      if (!this.sharedState.message) {
        this.$message({
          type: 'error',
          message: '请先登入',
        })
        return
      } else {
        this.username = this.sharedState.message
        fn()
      }
    },
    showOnline() {
      this.isLogin(() => {
        this.isOnlineGameShow = true
      })
    },
    showChat() {
      this.isLogin(() => {
        this.isChatShow = true
      })
    },
    showDnf() {
      this.isLogin(() => {
        this.isDnfShow = true
      })
    },
    showUserCenter() {
      this.isLogin(() => {
        this.isUserCenterStatus = true
      })
    },
    closeUserCenter() {
      this.isUserCenterStatus = false
    },
    async getusername() {
      const res = await this.$http.post(`u/getusername`, this.model)
      this.username = res.data.username
      this.id = res.data._id
    },
    getTime() {
      var time = new Date()
      var hours = time.getHours() > 9 ? time.getHours() : '0' + time.getHours() //时
      var minutes =
        time.getMinutes() > 9 ? time.getMinutes() : '0' + time.getMinutes() //分
      var seconds =
        time.getSeconds() > 9 ? time.getSeconds() : '0' + time.getSeconds() //秒
      var mydate = hours + ':' + minutes + ':' + seconds
      this.nowDateTime = mydate
    },
  },
  components: {
    UserCenter,
    Dnf,
    onlineGame,
    Chat: () => import('./../components/chat'),
  },
}
</script>

